import React, { Component } from 'react'
import ReactHtmlParser from 'react-html-parser'; 
import PopUpVideo from './PopUpVideo';
import { MediaBaseUrl } from '../../../../../../actions/api';

class Media extends Component {
    state = {
        isVideo: false,
    };
    componentDidMount(){
        this.setState({ isVideo: this.props.autoPlay });
    }
    onClickToPlay = (event) => {
        if(this.props.clickToPlay){
            event.preventDefault();
            this.setState({ isVideo: true });
        }
    };

    render() {
        const { item, POPUP_IMAGE_RENEW_REQUEST } = this.props;

         console.log(this.props,'dsjfklsdjfjklsdflsldfsjfsdfjjj')

        const { isVideo } = this.state;

        return item && Object.keys(item).length > 0 ? (
            <>
                {(!isVideo && item.postFile != "" && item.postFile != null) ? 
                (!item.mediaClip || item.mediaClip.length == 0 || item.mediaClip == 'undefined' || item.isAudio) ? (
                    item.networkId == 28 ? (
                        <img
                            className={`img-fluid initLazyLoad imgID${item.id}`}
                            src={item.newMedia}
                            data-id={item.id}
                            id={`imgID${item.id}`}
                            onClick={this.onClickToPlay}
                            data-load="0"
                            data-item-id={item.id}
                            data-network={item.networkId}
                            onError={(event) => item.stories == 0 ? POPUP_IMAGE_RENEW_REQUEST(event) : null}
                        />
                        
                    ) : (
                        <img
                            src={item.newMedia}
                            className={`img-fluid initLazyLoad imgID${item.id}`}
                            err-SRC
                            data-id={item.id}
                            data-filter={item.feedFilter}
                            data-type={item.type}
                            data-owner={item.createdBy}
                            data-network={item.networkId}
                            data-expired={item.expired}
                            data-status={item.status}
                            data-link={item.link}
                            onClick={this.onClickToPlay}
                            onError={(event) =>
                                item.stories == 0 ? POPUP_IMAGE_RENEW_REQUEST(event) : null
                            }
                            data-load="0"
                            data-item-id={item.id}
                            data-filter-id={item.filterId}
                        />
                    )
                ) : (
                    <div className='moderation_video_outer' onClick={this.onClickToPlay}>
                         <img class="play_icon" src={`${MediaBaseUrl}images/play.png`}  width={55} height={55} alt="play"/>
                         <PopUpVideo data={{...item, mediaUrl : item.mediaClip }} shorts={true}/>
                    </div>
                ) : isVideo ? (
                    <>
                        {item.isAudio ? (
                            <div
                                class="audioPlayer"
                                style={{ backgroundImage: `url(${item.newMedia})` }}
                            >
                                {ReactHtmlParser(item.mediaFile)}
                            </div>
                        ) : (
                            <PopUpVideo data={item} shorts={false}/>
                        )}
                    </>
                ) : null}
            </>
        ) : null;
    }
}

export default Media;