import React, { Component } from "react";
import ConnectButton from "../common/connectButton";
import AuthorInfo from "../../components/content/authorInfo";
import Tags from "../Tags";
import ShowMoreContent from "../common/showMoreContent";
import { SENTIMENTS_CALC } from "../../../../../utils";
import Media from "../common/media/Media";

class PostdetailPopup extends Component {
  render() {
    const {
      content,
      link,
      created,
      authorUsername,
      authorImage,
      network,
      postClicks,
      image,
      recommendationScore,
      postPerformance,
      sentiment,
      feedName,
      tags,
    } = this.props;

    console.log(this.props,'sdfjksjdfjsfjd')

    return (
      <div class="influential_contributors_custom_modal px-5 sw_theme">
        <div className="influential-data-list">
          <div className="row align-items-center">
            <div className="col-md-6">
              <div className="mb-5">
                <img src={image} className="img-fluid rounded-8" />
                {/* <Media {...this.props}/> */}
              </div>
            </div>
            <div className="col-md-6">
              <div className="d-flex justify-content-between align-items-center">
                <div className="d-flex align-items-center popup-author">
                  <AuthorInfo  picture={authorImage} username={authorUsername} date={created}/>
                </div>
                <div className="">
                  <i className={`fab ${network.icon} font-size-h2`}></i>
                </div>
              </div>

              <div className="mt-5">
                <ShowMoreContent classes={'influ_content'} content={content} idIndex={0} line={3} />
              </div>
              <div className="popup-tags-list mt-5">
                 <Tags tags={tags}/>
               </div>
              <div className="popup-featur-list mt-5">
                <ul className="m-0">
                  <li>
                    <p className="d-flex" ><span className="w-50">AI Recommendation</span> <strong className="w-50 d-flex align-items-center"><b className="bulits bg-success d-block me-2 rounded-circle size-12"></b> Recommended ({recommendationScore}%)</strong></p>
                  </li>
                  <li>
                    <p className="d-flex"><span className="w-50">Impressions</span> <strong className="w-50">{postPerformance}</strong></p>
                  </li>
                  <li>
                    <p className="d-flex"><span className="w-50">Post Clicks</span> <strong className="w-50">{postClicks}</strong></p>
                  </li>
                  <li>
                    <p className="d-flex"><span className="w-50">Sentiment</span> <strong className="w-50"> {SENTIMENTS_CALC(sentiment).label} </strong></p>
                  </li>
                  <li>
                    <p className="d-flex"><span className="w-50">Feed Name</span> <strong className="w-50">#{feedName}</strong></p>
                  </li>
                </ul>
              </div>
              <div className="conn_status connnected popup-connnected mt-10 mb-5">
                <ConnectButton link={link} />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PostdetailPopup;
